.navbar{
    display: flex;
    justify-content: space-around;
    padding: 10px;
    box-shadow: 0 1px 3px -2px;

}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: black;
    font-size: 40px;
    font-weight: 600;
}
.nav-menu{
display: flex;
align-items: center;
list-style: none;
gap: 50px;
color: black ;
font-size: 20px;
font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
    /* text-decoration: none; */
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background-color: orange;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px; 
}
.nav-login-cart button{
    width: 150px;
    height: 60px;
    outline: none;
    border: 1px solid black;
    border-radius: 75px;
    color: grey;
    font-size: 20px;
    font-weight: 500px;
    background-color: white;
    cursor: pointer;
}
.nav-login-cart button:active{
    background-color: azure;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background-color: red;
    color: white;
}