.offers{
    width: 75%;
    height: 80vh;
    display: flex;
    margin: auto;
    padding: 100px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg , white, grey 60%);
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color: #000;
    font-size: 70px;
    font-weight: 600;
}
.offers-left p{
    color: #000;
    font-size: 22px;
    font-weight: 600;
}
.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: red;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 50px;

}